.shop {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 40px;
}

.product-item {
  text-decoration: none;
  color: inherit;
  font-family: 'Helvetica Neue', Arial, sans-serif;
}

.product-image-container {
  position: relative;
  padding-bottom: 133%;
  overflow: hidden;
}

.product-item img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.product-item:hover img {
  transform: scale(1.05);
}

.product-info {
  margin-top: 10px;
  text-align: left;
}

.product-item h3 {
  font-weight: 300;
  margin: 0;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 1px;
}

.product-item p {
  font-weight: 300;
  font-size: 14px;
  margin: 5px 0 0;
}


.product-grid.collage {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 200px;
  gap: 10px;
}

.product-item.collage {
  position: relative;
  overflow: hidden;
}

.product-item.collage:nth-child(4n+1) {
  grid-column: span 2;
  grid-row: span 2;
}

.product-item.collage:nth-child(8n+2) {
  grid-column: span 2;
}

.product-item.collage:nth-child(8n+5) {
  grid-row: span 2;
}

.product-item.collage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product-item.collage .product-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px;
  transform: translateY(100%);
  transition: transform 0.3s ease;
}

.product-item.collage:hover .product-info {
  transform: translateY(0);
}