.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    background-color: white;
    padding: 2rem;
    border-radius: 8px;
    max-width: 90%;
    width: 400px;
    max-height: 90vh;
    overflow-y: auto;
    position: relative;
  }
  
  .modal-close {
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  .modal-options {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .modal-option {
    background: none;
    border: 1px solid #000;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .modal-option:hover,
  .modal-option.active {
    background-color: #000;
    color: #fff;
  }
  
  .modal-section {
    margin-bottom: 1.5rem;
  }
  
  .price-range {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .brand-options {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .brand-option {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .modal-apply {
    background-color: #000;
    color: #fff;
    border: none;
    padding: 0.75rem 1rem;
    font-size: 1rem;
    cursor: pointer;
    width: 100%;
    transition: background-color 0.3s ease;
  }
  
  .modal-apply:hover {
    background-color: #333;
  }